#footer {
    width           : 100%;
    height          : 40vh;
    background-color: rgba(0, 0, 0, 0.5);
    display         : flex;
    flex-direction  : column;
    justify-content : center;
    align-items     : center;
}

h4 {
    color      : var(--color-primary);
    font-family: 'Times New Roman', Times, serif;
}

h4:first-child {
    font-size    : 2rem;
    margin-top   : auto;
    margin-bottom: 1rem;
}

h4:nth-child(2) {
    font-size    : 2.4rem;
    text-shadow  : 1px 0px 1px #fff;
    margin-bottom: auto;
}

span {
    margin-bottom: 0.5rem;
    color        : var(--color-text);
}

@media screen and (max-width: 1024px) {
    #footer{
        height: 25vh;
        justify-content: flex-start;
    }
    h4:first-child {
        font-size: 1.4rem;
        margin-top: 3rem;
    }

    h4:nth-child(2) {
        font-size: 1.6rem;
    }
}

@media screen and (max-width: 900px) {
    #footer{
        height: 20vh;
        justify-content: flex-start;
    }
    h4:first-child {
        font-size: 1.2rem;
        margin-top: 2rem;
    }

    h4:nth-child(2) {
        font-size: 1.4rem;
    }
}